import {
    BaseInput,
    booleanConverter,
    createClassName,
    defineElement,
    html,
    property,
    query,
    state,
} from '@umetrics/sartorius-ui-base-input';
import {
    DatePeriodPickerProperties,
    DatePeriodPickerTextKey,
    DatePeriodPickerTexts,
} from './datePeriodPickerProperties';
import { version } from './metadata';
import {
    DatePicker as DatePickerT,
    DatePickerCalendarPosition,
    DatePickerCustomizeDate,
    DatePickerEvent,
} from '@umetrics/sartorius-ui-date-picker';
import { datePeriodPickerStyles } from './datePeriodPickerStyle';
import { DatePeriodPickerEvent, DatePeriodPickerEventType } from './datePeriodPickerEvent';

@defineElement({
    tagName: 'date-period-picker',
    styles: datePeriodPickerStyles,
    version,
    //sub: [DatePicker, FeedbackMessage],
})
export class DatePeriodPicker extends BaseInput<DatePeriodPicker> implements DatePeriodPickerProperties {
    // Static members
    private static DatePicker;

    // Public members
    @property({ type: String, reflect: false })
    placeholder = '';
    @property({ type: Boolean, converter: booleanConverter, reflect: false })
    required = false;

    @property({ type: String, attribute: 'value-start', reflect: true })
    valueStart = '';
    @property({ type: String, attribute: 'value-end', reflect: true })
    valueEnd = '';
    @property({ type: Function })
    customizeDate?: DatePickerCustomizeDate;
    @property({ type: String, reflect: false, attribute: 'calendar-position' })
    calendarPosition = DatePickerCalendarPosition.Absolute;

    @property({ type: String, reflect: true })
    get value() {
        const json = { valueStart: this.valueStart, valueEnd: this.valueEnd };
        return JSON.stringify(json);
    }

    set value(value: string) {
        if (value) {
            const json = JSON.parse(value);
            this.valueStart = json['valueStart'];
            this.valueEnd = json['valueEnd'];
        }
    }

    @property({ type: Object, attribute: false })
    get texts() {
        return this._texts;
    }

    set texts(value) {
        const prevValue = this._texts;
        if (typeof value === 'string') {
            value = JSON.parse(value);
        }
        this._texts = value;
        this.requestUpdate('texts', prevValue);
    }

    // Private members
    private _observer?: MutationObserver;
    @query("[class*='-pick-start']")
    private _startElem;
    @query("[class*='-pick-end']")
    private _endElem;
    private _texts: DatePeriodPickerTexts = {
        [DatePeriodPickerTextKey.INVALID]: 'Invalid dates format',
        [DatePeriodPickerTextKey.INVALID_START]: 'Invalid start date format',
        [DatePeriodPickerTextKey.INVALID_END]: 'Invalid end date format',
    };
    @state()
    private _invalid: string | null = null;
    // Protected members

    // Public members

    // Public methods

    constructor() {
        super();
    }

    focus(options?: FocusOptions) {
        const start = this._getDatepickerStartElement();
        if (start) {
            start.focus(options);
        }
        // This is not correct
    }

    blur() {
        const start = this._getDatepickerStartElement();
        if (start) {
            start.blur();
        }
        // This is not correct
    }

    protected _getChildImports() {
        const imports: any[] = [];
        imports.push({ module: () => import('@umetrics/sartorius-ui-date-picker'), elem: 'DatePicker' });
        return imports;
    }

    protected firstUpdated(_changedProperties) {
        super.firstUpdated(_changedProperties);

        const attribScanned = ['hover', 'focused', 'invalid'];
        this._observer = new MutationObserver((mutationList) =>
            mutationList.forEach((mutation) => {
                const attrib = mutation.attributeName;
                if (attrib && attribScanned.findIndex((r) => r === attrib) > -1) {
                    if (attrib === 'invalid') {
                        this._manageInvalidPickers();
                    } else {
                        if (mutation.target === this._startElem) this._endElem[attrib] = !!this._startElem[attrib];
                        if (mutation.target === this._endElem) this._startElem[attrib] = !!this._endElem[attrib];
                    }
                }
            })
        );
        [this._startElem, this._endElem].forEach((e) =>
            this._observer!.observe(e, {
                attributeFilter: attribScanned,
                attributeOldValue: true,
                subtree: true,
            })
        );
    }

    protected render() {
        const [baseClassName, rootClasses] = createClassName(this.tagName.toLowerCase(), {
            disabled: !!this.disabled,
            required: !!this.required,
        });

        return html`
            <label class="${rootClasses}">
                ${this._renderLabel(baseClassName)}
                <div class="${baseClassName}-wrapper">
                    <${DatePeriodPicker.DatePicker.tag}
                        class="${baseClassName}-pick-start"
                        .periodPickerStyle=${true}
                        ?disabled="${!!this.disabled}"
                        value="${this.valueStart}"
                        placeholder=${this.placeholder}
                        end=${this.valueEnd}
                        end-is-selected-date=true
                        .calendarPosition=${this.calendarPosition}
                        @change=${this._handleStartDateChange}
                    ></${DatePeriodPicker.DatePicker.tag}>
                    <${DatePeriodPicker.DatePicker.tag}
                        class="${baseClassName}-pick-end"
                        .periodPickerStyle=${true}
                        ?disabled="${!!this.disabled}"
                        value="${this.valueEnd}"
                        placeholder=${this.placeholder}
                        start="${this.valueStart}"
                        start-is-selected-date=true
                        .calendarPosition=${this.calendarPosition}
                        @change=${this._handleEndDateChange}
                    ></${DatePeriodPicker.DatePicker.tag}>
                </div>
                <div class="${baseClassName}-feedback-wrapper">
                    ${this._renderFeedback(baseClassName, this._invalid !== null ? this._texts[this._invalid] : '')}
                </div>
            </label>
        `;
    }

    private _manageInvalidPickers() {
        const valid = (s) => s.invalid && s.invalid !== 'period';
        const validStart = valid(this._startElem);
        const validEnd = valid(this._endElem);

        if (validStart && validEnd) {
            this._invalid = DatePeriodPickerTextKey.INVALID;
        } else if (validStart) {
            this._invalid = DatePeriodPickerTextKey.INVALID_START;
            this._endElem.invalid = 'period';
        } else if (validEnd) {
            this._invalid = DatePeriodPickerTextKey.INVALID_END;
            this._startElem.invalid = 'period';
        } else {
            this._invalid = null;
            this._startElem.invalid = null;
            this._endElem.invalid = null;
        }
    }

    private async _handleStartDateChange(e: DatePickerEvent) {
        e.stopPropagation();
        this.valueStart = e.target.value;

        await this.updateComplete;

        this.dispatchEvent(
            new DatePeriodPickerEvent(DatePeriodPickerEventType.ChangeStart, { detail: this.valueStart })
        );
        this.dispatchEvent(new DatePeriodPickerEvent(DatePeriodPickerEventType.Change, { detail: this.value }));

        const endElement = this._getDatepickerEndElement();

        if (endElement) {
            endElement.start = this.valueStart;
            endElement.focus();
        }
    }

    private async _handleEndDateChange(e: DatePickerEvent) {
        e.stopPropagation();
        this.valueEnd = e.target.value;
        await this.updateComplete;

        this.dispatchEvent(new DatePeriodPickerEvent(DatePeriodPickerEventType.ChangeEnd, { detail: this.valueEnd }));
        this.dispatchEvent(new DatePeriodPickerEvent(DatePeriodPickerEventType.Change, { detail: this.value }));

        const endElement = this._getDatepickerEndElement();
        if (endElement) {
            endElement.blur();
        }
    }

    private _getDatepickerStartElement() {
        return this.shadowRoot && this.shadowRoot.querySelector<DatePickerT>(`[class*="-pick-start"]`);
    }

    private _getDatepickerEndElement() {
        return this.shadowRoot && this.shadowRoot.querySelector<DatePickerT>(`[class*="-pick-end"]`);
    }
}
