import {
    baseInputStyle,
    css,
    CSSResultArray,
    cssSelector,
    cssVariableAsVar,
    GenerateStyles,
} from '@umetrics/sartorius-ui-base-input';

export const datePeriodPickerStyles: GenerateStyles = (tagName) => {
    const { root, child, is } = cssSelector(tagName);

    return [
        baseInputStyle(tagName),
        css`
            :host {
                display: block;
            }

            ${root} {
                display: flex;
                flex-direction: column;
            }

            ${child('border')} {
                border: 1px solid ${cssVariableAsVar('color_secondary_4')};
                display: flex;
                align-items: center;
                justify-content: space-between;
                min-width: 0;
                width: 100%;
            }

            ${child('wrapper')} {
                display: flex;
                flex-direction: row;
            }

            ${is('focused')} ${child('border')} {
                border: 1px solid ${cssVariableAsVar('color_primary_3')};
            }

            ${is('validation')} ${child('border')} {
                outline: none;
                border: 1px solid ${cssVariableAsVar('color_traffic_light_1')};
            }

            ${is('disabled')} ${child('border')} {
                outline: none;
                border: 1px solid ${cssVariableAsVar('color_secondary_3')};
                background-color: ${cssVariableAsVar('color_secondary_2')};
            }

            ${child('input-row')} {
                position: relative;
                display: flex;
                flex-direction: column;
            }

            ${child('input')} {
                flex: 1;
                min-width: 80px;
                padding: 10px 15px;
                height: 100%;
                border: none;
                outline: none;
                border-radius: 0;
                font-size: 14px;
                color: ${cssVariableAsVar('color_primary_3')};
                background: transparent;
                text-align: initial;
            }

            ${child('input')}::placeholder {
                font-size: 14px;
                color: ${cssVariableAsVar('color_secondary_7')};
            }

            ${is('disabled')} ${child('input')} {
                color: ${cssVariableAsVar('color_secondary_7')};
            }

            ${child('input')}:focus {
                outline: none;
                border: none;
            }

            ${child('input')}:focus-visible {
                outline: none;
                border: none;
            }

            ${child('calendar')} {
                visibility: hidden;
                opacity: 0;
                height: 0;
                transform: scale(0);
                transform-origin: top left;
                transition:
                    visibility 0s linear 300ms,
                    height 0s,
                    opacity 300ms,
                    transform 300ms;
            }
            ${is('pos-fixed')} ${child('calendar')} {
                position: fixed;
            }
            ${is('pos-absolute')} ${child('calendar')} {
                position: absolute;
                top: 100%;
            }
            ${is('pos-none')} ${child('calendar')} {
                display: none;
            }

            ${is('show')} ${child('calendar')} {
                visibility: visible;
                opacity: 1;
                height: auto;
                transform: scale(1);
                transition:
                    visibility 0s linear 0s,
                    opacity 300ms,
                    transform 300ms;
            }
        `,
    ] as CSSResultArray;
};
