import { BaseInputProperties } from '@umetrics/sartorius-ui-base-input';
import { DatePickerCalendarPosition } from '@umetrics/sartorius-ui-date-picker';

export enum DatePeriodPickerTextKey {
    INVALID = 'invalid',
    INVALID_START = 'invalid_start',
    INVALID_END = 'invalid_end',
}
export interface DatePeriodPickerTexts {
    [DatePeriodPickerTextKey.INVALID]?: string;
    [DatePeriodPickerTextKey.INVALID_START]?: string;
    [DatePeriodPickerTextKey.INVALID_END]?: string;
}

export interface DatePeriodPickerProperties extends BaseInputProperties {
    texts?: DatePeriodPickerTexts;
    placeholder?: string;
    required?: boolean;
    valueStart?: string;
    valueEnd?: string;
    calendarPosition?: DatePickerCalendarPosition;
}
